import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/pages/Home'
import SignIn from '@/pages/SignIn'
import Search from '@/pages/Search'
import Article from '@/pages/Article'
import Catalog from '@/pages/Catalog'
import Profile from '@/pages/Profile'
import Checkout from '@/pages/Checkout'
import Pricing from '@/pages/Pricing'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Home
    },
    {
      path: '/entrar',
      name: 'login',
      component: SignIn,
      meta: {
        hiddeNav: true
      }
    },
    {
      path: '/u/:username',
      component: Profile,
      props: true,
      children: [
        {
          path: '',
          name: 'profile',
          component: {
            render: h => {
              return h('hr')
            }
          }
        },
        {
          path: 'catalogo',
          name: 'profile.catalog',
          component: Catalog
        }
      ]
    },
    {
      path: '/precios',
      name: 'pricing',
      component: Pricing
    },
    {
      path: '/checkout',
      name: 'checkout',
      component: Checkout
    },
    {
      path: '/buscar/:search?',
      name: 'search',
      component: Search,
      props: true
    },
    {
      path: '/p/:name/:id',
      name: 'article',
      component: Article,
      props: true
    },
  ]
})



