<template>
<div class="row">
  <div v-if="mode === 'nav-list'">
    <div class="col-xs-12 col-sm-8">
      <ul class="list-unstyled row">
        <div v-if="!filteredData || !filteredData.length">
          <h2 class="panel-body">No se encontraron resultados</h2>
        </div>
        <li v-for="(p,i) in filteredData" v-else>
          <a href="#" @click.prevent="goToArticle(i, p.title)">{{p.title}}</a>
        </li>
        <li role="separator" class="divider visible-xs"></li>
        <li class="visible-xs">
          <a href="#"><strong>Pantalones todos rotos y sucios</strong></a>
        </li>
      </ul>
    </div>
    <div class="col-sm-4 hidden-xs" style="border-left: 1px solid lightgrey">
      <small class="text-muted">Publicidad:</small>
      <div class="panel panel-default">
        <div class="media">
          <div class="media-left media-middle panel-body">
            <img src="http://placehold.it/48x48" alt="" class="media-object">
          </div>
          <div class="media-body">
            <h4>
              Pantalones todos rotos y sucios <br>
              <small>$ 4.555,60 - Art. usado</small>
            </h4>
            <div class="rating hidden-sm">
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
            </div>
          </div>
        </div>
      </div>
      <div class="panel panel-default" v-if="filteredData.length > 3">
        <div class="media">
          <div class="media-left media-middle panel-body">
            <img src="http://placehold.it/48x48" alt="" class="media-object">
          </div>
          <div class="media-body">
            <h4>
              Pantalones todos rotos y sucios <br>
              <small>$ 4.555,60 - Art. usado</small>
            </h4>
            <div class="rating hidden-sm">
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
              <i class="fa fa-star"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-else>
    <div v-if="!data || !data.length">
      <h2 class="panel-body">No se encontraron resultados</h2>
    </div>
    <div v-for="(p, i) in data" class="col-xs-12 col-sm-4 col-md-3" v-else-if="mode === 'grid'">
      <div class="panel panel-default" style="min-height:400px">
        <img :src="p.img" alt="p.title" class="img-responsive">
        <div class="panel-body">
          <div>
            <h3><a href="#" @click.prevent="goToArticle(i, p.title)">{{ p.title.substring(0, 60) }}</a></h3>
            <p>$ {{ p.price.toLocaleString() }}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="col-xs-12">
      <div class="text-center">
        <ul class="pagination">
          <li class="page-item disabled">
            <a class="page-link" href="#">&laquo;</a>
          </li>
          <li class="page-item active">
            <a class="page-link" href="#">1</a>
          </li>
          <li class="page-item">
            <a class="page-link" href="#">2</a>
          </li>
          <li class="page-item">
            <a class="page-link" href="#">3</a>
          </li>
          <li class="page-item">
            <a class="page-link" href="#">4</a>
          </li>
          <li class="page-item">
            <a class="page-link" href="#">5</a>
          </li>
          <li class="page-item">
            <a class="page-link" href="#">&raquo;</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'search-results',
  props: {
    mode: {
      default: 'nav-list'
    },
    data: Array
  },
  computed: {
    filteredData () {
      return this.data.filter((item) => {
        return item.title.indexOf(this.$root.search) != -1
      }).slice(0, 6)
    }
  },
  methods: {
    goToArticle(id, title) {
      console.log('Going to', id, encodeURI(title.replace(/ /g, '-')))
      this.$router.push({
        name: 'article', params: {name: encodeURI(title.replace(/ /g, '-')), id}
      })
    }
  }
}
</script>

<style lang="sass" scoped>
.list-unstyled
  li
    &:hover
      background: #2780e3
      a
        color: #FFF
    a
      display: block
      padding: 8px 15px
      text-decoration: none
.dropdown-menu
  width: 100%
h3
  font-size: 1.2em
</style>



