<template>
<form @submit.prevent="$emit('submit')" :class="{'is-focus': isFocused}">
  <div>
    <div class="form-group">
      <div class="input-group">
        <input type="text"
          class="form-control"
          v-model="$root.search"
          placeholder="¡Buscá en Minas!"
          @focus="onFocus"
          @blur="onBlur"
          ref="input">
        <span class="input-addon" @click="$root.search = ''">
          <i class="fa fa-close" v-if="$root.search.length"></i>
          <i class="fa fa-search" v-else></i>
        </span>
      </div>
    </div>
  </div>
</form>
</template>

<script>
export default {
  data () {
    return {
      isFocused: false
    }
  },
  methods: {
    blur () {
      this.$refs.input.blur()
    },
    onFocus () {
      this.$emit('focused')
      this.isFocused = true
    },
    onBlur () {
      setTimeout(() => { // fix for item clicks
        this.$emit('blurred')
        this.isFocused = false
      }, 100);
    }
  }
}
</script>



