<template>
  <div id="app">
    <navbar :is-open="navbarOpen" @toggle="navbarOpen = !navbarOpen" v-if="!$route.meta.hiddeNav"></navbar>
    <router-view :style="{paddingTop: $route.name === 'index' ? 0 : '30px'}"/>
    <div class="modal fade in" :class="{'in': modalOpen}" :style="{display: modalOpen ? 'block' : 'none'}">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h3 class="modal-title">Nueva publicación</h3>
            <button type="button" class="close"  @click.prevent="modalOpen = false" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-xs-12">
                <div class="alert alert-success text-center" style="cursor:pointer">
                  <h2>Producto</h2>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-xs-12 col-sm-6">
                <div class="alert alert-info" style="cursor:pointer">
                  <h2>Servicio</h2>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6">
                <div class="alert bg-primary" style="cursor:pointer">
                  <h2>Evento</h2>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary">Siguiente</button>
            <button type="button" class="btn btn-secondary" @click.prevent="modalOpen = false">Cancelar</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop fade" v-if="modalOpen" :class="{'in': modalOpen}" @click.prevent="modalOpen = false"></div>
  </div>
</template>

<script>
// CSS
import '@/assets/sass/app.scss'

import Vue from 'vue'
import navbar from '@/components/navbar/navbar'
import searchResults from '@/components/searchResults/searchResults'
Vue.component(searchResults.name, searchResults)

export default {
  name: 'app',
  data () {
    return {
      navbarOpen: false,
      search: '',
      modalOpen: false
    }
  },
  components: {
    navbar
  }
}
</script>



