<template>
<div>
  <div class="container">
    <h1 class="page-header">{{ data.length }} Resultados de "<i class="highlight">{{searchQuery}}</i>"</h1>
  </div>
  <div class="container-fluid">
    <div class="panel panel-default col-md-2">
      <div class="panel-body">
        <ul class="list-unstyled">
          <li><strong>Categorías</strong></li>
          <li><a href="#">asd</a></li>
          <li><a href="#">asd</a></li>
          <li><a href="#">asd</a></li>
          <li><a href="#">asd</a></li>
          <li><a href="#">asd</a></li>
        </ul>
      </div>
    </div>
    <div class="col-md-10">
      <search-results :data="data" mode="grid"></search-results>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: ['search'],
  computed: {
    parsedSearch () { // because of SEO replacements like "-"
      return this.search.replace(/-/g, ' ')
    },
    searchQuery () {
      return this.$root.search ? this.$root.search : this.parsedSearch
    },
    data () {
      return this.$root.db.jeans.filter(item => {
        return item.title.indexOf(this.searchQuery) > -1
      })
    }
  },
  created () {
    if (this.parsedSearch) this.$root.search = this.parsedSearch
  }
}
</script>

<style lang="sass">
i.highlight
  color: #0061c7
</style>



