<template>
  <div id="login" class="bg-primary">
    <!--<div class="bg"></div>-->
    <div class="row card">
      <div class="col-xs-12">
        <div class="card-body panel-body text-center">
          <br>
          <img src="http://placehold.it/100x100?text=Logo" class="img-responsive center-block form-group">
          <button class="btn btn-primary btn-block">
            <i class="fa fa-facebook icon"></i> Entra con Facebook
          </button>
          <button class="btn btn-danger btn-block">
            <i class="fa fa-google-plus icon"></i> Entra con Google
          </button>
          <button class="btn btn-info btn-block">
            <i class="fa fa-twitter icon"></i> Entra con Twitter
          </button>
          <div class="row panel-body">
            <div class="col-xs-5 o-ers"></div>
            <div class="col-xs-2">o</div>
            <div class="col-xs-5 o-ers"></div>
          </div>
          <form action="" @submit.prevent="submit">
            <div class="form-group">
              <v-input v-model="username" placeholder="Usuario">
                <template slot="prepend">
                  <i class="fa fa-user"></i>
                </template>
              </v-input>
            </div>
            <div class="form-group">
              <v-input type="password" v-model="password" placeholder="Contraseña">
                <template slot="prepend">
                  <i class="fa fa-key"></i>
                </template>
              </v-input>
            </div>
            <div class="form-group">
              <button type="submit" class="btn btn-success btn-block">
                <i class="fa fa-sign-in icon"></i> Iniciar sesión
              </button>
            </div>
          </form>
          <div class="form-group">
            <span>¿Aún no tienes una cuenta?</span>
            <button class="btn btn-primary btn-block" @click.prevent="submit">
              <i class="fa fa-sign-in icon"></i> Registrar cuenta
            </button>
          </div>
        </div>
        <!-- <div class="card-footer text-center">
          <small class="text-muted">Con la tecnología de <strong>SutilSoft</strong></small>
        </div> -->
      </div>
      <!--<div class="col-xs-12 col-md-6 visible-md visible-lg">
        <div class="card-body text-center center-block">
          <br><br>
          <img src="http://placehold.it/100x100" alt="" class="img-responsive center-block">
          <h3 style="
              letter-spacing: -2px;
              vertical-align: middle;
          ">¡Bienvenido a Admiral!</h3>
          <hr>
          <p class="lead">
            El panel de administración en la nube de SutilSoft
          </p>
        </div>-->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    submit () {
      this.$auth.logIn({
        username: this.username
      })
    }
  }
}
</script>

<style lang="sass">
#login
  padding: 70px 0px
  color: #333
  & > div.bg
    background: url('http://i062.radikal.ru/1310/58/ac4eaa31efdf.jpg') #000000
    background-size: cover
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    -webkit-filter: blur(5px)
    &:after
      content: ''
      background: rgba(0, 49, 45, 0.75)
      width: 100%
      height: 100%
      position: absolute
  & > .row
    max-width: 400px
    margin: 0 auto
    //background: url(http://i062.radikal.ru/1310/58/ac4eaa31efdf.jpg)
    //background-size: cover
    display: -webkit-box
    position: relative
    box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.6)
    & > div
      background: rgba(255, 255, 255, 0.95)
    & > div + div
      display: flex
      color: #FFF
      background: rgba(0, 49, 45, 0.75)
    img
      max-width: 100px
  .o-ers
    border-bottom: 1px solid #e7edee
    padding: 0.5rem
</style>



