<template>
<nav class="navbar navbar-inverse navbar-fixed-top">
  <div class="container dropdown" :class="{'open': $root.search.length}">
    <div class="navbar-header">
      <button type="button" class="navbar-toggle collapsed" @click.stop.prevent="$emit('toggle')" aria-expanded="false">
        <span class="sr-only">Toggle navigation</span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
      </button>
      <a class="navbar-brand" href="/">Logo</a>
      <search-form @submit.native="searchIt"  class="navbar-right" @focused="formFocus = true" @blurred="formFocus = false"  ref="searchForm"></search-form>
    </div>

    <div class="collapse navbar-collapse" :class="{'in': isOpen}">
      <ul class="nav navbar-nav navbar-right" v-if="!$auth.check">
        <li>
          <a href="#" class="btn btn-primary btn-navbar ">Sobre nosotros</a>
        </li>
        <li>
          <a href="#" @click.prevent="$router.push({name: 'pricing'})" class="btn btn-primary btn-navbar ">Precios</a>
        </li>
        <li>
          <a href="#" @click.prevent="$router.push({name: 'login'})" class="btn btn-primary btn-navbar ">Entrar</a>
        </li>
      </ul>
      <ul class="nav navbar-nav navbar-right" v-else>
        <li>
          <a href="#">
            <span><i class="fa fa-bell-o"></i></span>
          </a>
        </li>
        <li>
          <a href="#">
            <span><i class="fa fa-envelope-o"></i></span>
          </a>
        </li>
        <li>
          <a href="#" @click.prevent="$router.push({name: 'profile'})">
            Cuenta
          </a>
        </li>
        <li>
          <a href="#" @click.prevent="$parent.modalOpen = true">
           <strong> Publicar</strong>
          </a>
        </li>
      </ul>
    </div><!-- /.navbar-collapse -->
    <search-results class="dropdown-menu" :data="$root.db.jeans" mode="nav-list" v-if="formFocus"></search-results>
  </div><!-- /.container-fluid -->
</nav>
</template>

<script>
import searchForm from '@/components/searchForm/searchForm'

export default {
	props: ['items', 'isOpen'],
  data () {
    return {
      formFocus: false
    }
  },
  methods: {
    searchIt () {
      let SEOstring = this.$root.search.replace(/ /g, '-')
      this.$router.push({name: 'search', params: {search: SEOstring}})
      this.$refs.searchForm.blur()
    }
  },
  components: {
    searchForm
  }
}
</script>



