<template>
<div class="container-fluid">
  <div class="panel panel-default">
    <div class="panel-body">
      <div class="clearfix">
        <div class="col-xs-3 col-sm-4 col-md-3">
          <img src="http://placehold.it/200x200" alt="" class="img-responsive img-circle">
        </div>
        <h1>{{ username }}</h1>
        <p class="lead">
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quibusdam a ex impedit! Distinctio recusandae ipsum, velit et perspiciatis suscipit tenetur animi libero quos esse rem perferendis iusto totam cum sint.
        </p>
      </div>
    </div>
    <ul class="nav nav-tabs nav-justified" style="margin-bottom: -2px">
      <li class="nav-item" :class="{'active': $route.name === 'profile'}">
        <a class="nav-link" href="#" @click.prevent="$router.push({name: 'profile', params: {username}})">Perfíl</a>
      </li>
      <li class="nav-item" :class="{'active': $route.name === 'profile.catalog'}">
        <a class="nav-link" href="#" @click.prevent="$router.push({name: 'profile.catalog', params: {username: $auth.user.username}})">Catálogo</a>
      </li>
    </ul>
  </div>
  <div class="row">
    <router-view></router-view>
  </div>
</div>
</template>

<script>
export default {
  props: ['username']
}
</script>



