<template>
<div class="container-fluid">
  <div v-if="loading" class="loader">
    <h2><i class="fa fa-spinner fa-pulse fa-fw"></i> <br> Cargando</h2>
  </div>
  <div v-else>
    <div class="row">
      <ol class="breadcrumb">
        <li><a href="#"><small class="fa fa-chevron-left"></small> Volver al listado</a></li>
        <li><a href="#">Ropa, calzado y accesorios</a></li>
        <li><a href="#">Jeans</a></li>
      </ol>
    </div>
    <div class="col-xs-12 col-sm-6 col-md-5 col-lg-7">
      <img :src="item.img" :alt="item.title" class="center-block img-responsive" style="width:100%">
      <div class="btn-group btn-group-justified">
        <div class="btn-group">
          <img :src="'http://placehold.it/600x600'" :alt="item.title" class="btn">
        </div>
        <div class="btn-group">
          <img :src="'http://placehold.it/600x600'" :alt="item.title" class="btn">
        </div>
        <div class="btn-group">
          <img :src="'http://placehold.it/600x600'" :alt="item.title" class="btn">
        </div>
        <div class="btn-group">
          <img :src="'http://placehold.it/600x600'" :alt="item.title" class="btn">
        </div>
      </div>
    </div>
    <div class="col-xs-12 col-sm-6 col-md-7 col-lg-5">
      <div class="panel panel-success">
        <div class="panel-heading">
          <h4>
           Vendedor premium
          </h4>
        </div>
        <div class="panel-body">
          <div class="row form-group">
            <div class="col-xs-6">
              <div class="text-center"><i class="fa fa-map-marker"></i> Av. la calle 666</li></div>
              <a class="btn btn-sm btn-block btn-primary"><i class="fa fa-globe"></i> Ubicación</a>
            </div>
            <div class="col-xs-6">
              <div class="text-center"><i class="fa fa-phone"></i> (+598) 2 22 22</div>
              <a class="btn btn-sm btn-block btn-info"><i class="fa fa-phone"></i> Llamar</a>
            </div>
          </div>
          <a href="#" class="btn btn-block btn-primary"
            @click.prevent="$router.push({name: 'profile.catalog', params: {'username': 'barraca.walter'}})">
            <i class="fa fa-shopping-cart"></i> Catálogo del vendedor
          </a>
        </div>
      </div>
      <h1>
        {{item.title}}
      </h1>
      <h2 class="page-header">
        <strong>$ {{ item.price.toLocaleString() }}</strong>
      </h2>
      <div>
        <div class="btn-toolbar form-group" role="toolbar">
          <!--<div class="btn-group" role="group">...</div>
          <div class="btn-group" role="group">...</div>-->
          <div class="btn-group btn-group-sm" role="group">
            <button type="button" class="btn btn-primary"><i class="fa fa-facebook"></i></button>
            <button type="button" class="btn btn-success"><i class="fa fa-whatsapp"></i></button>
            <button type="button" class="btn btn-danger"><i class="fa fa-google-plus"></i></button>
            <button type="button" class="btn btn-default"><i class="fa fa-envelope"></i></button>
          </div>
        </div>
      </div>
      <div class="well">
        <form action="#" class="form-horizontal">
          <div class="panel-body text-center">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Dolore blanditiis, tempore perspiciatis, amet cupiditate, iure praesentium expedita sunt ipsa incidunt omnis doloribus, voluptates doloremque odit et debitis deserunt eligendi tenetur.
          </div>
          <!--<div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label">Email</label>
            <div class="col-sm-10">
              <input type="email" class="form-control" id="inputEmail3" placeholder="Email">
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label">Asdasdasd</label>
            <div class="col-sm-10">
              <select name="" id="" class="form-control">
                <option value="" selected>Test</option>
              </select>
            </div>
          </div>-->
        </form>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: ['name', 'id'],
  data () {
    return {
      loading: true,
      item: {}
    }
  },
  methods: {
    fetchData () {
      setTimeout(() => {
        this.item = this.$root.db.jeans.find((item, index) => {
            return index === parseInt(this.id)
        })
        this.loading = false
      }, 0x0000)
    }
  },
  created () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  }
}
</script>

<style lang="sass" scoped>
.loader
  height: 100%
  width: 100%
  position: absolute
  right: 0
  top: 0
  display: table
  h2
    vertical-align: middle
    display: table-cell
    text-align: center
</style>



