import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

Vue.prototype.$auth = new Vue({
  data () {
    return {
      check: false,
      user: null
    }
  },
  methods: {
    logIn (user) {
      this.user = user
      this.check = true
      router.push({name: 'index'})
    }
  }
})

import Input from './elements/input'
Vue.use(Input)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  data: {
    search: '',
    db: {
      jeans: require('@/data/jeans').default
    }
  },
  router,
  render: h => h(App)
})



